.container {
  max-width: 800px;
  margin: 0 auto;
  height: 100vh;
  padding: 20px;
}
h1 {
  text-align: center;
  margin-bottom: 2rem;
}
body {
  background-color: aliceblue;
}
form {
  /* padding: 1rem; */
  background-color: #fff;
  border-radius: 5px;
  padding: 1rem;
}
form * {
  margin: 5px;
  width: 100%;
}

/* label {
  margin-bottom: 10px;
  margin-right: 1rem;
} */

input {
  padding: 5px 10px;
  font-size: 10px;
  border: 1px solid gainsboro;
  border-radius: 5px;
  margin-top: 5px;
  margin-bottom: 10px;
  width: 90%;
}

button {
  padding: 5px 10px;
  background-color: royalblue;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: orange;
  padding: 0.8rem;
  margin-top: 1rem;
}
ul {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style-type: none;
  margin-right: 2rem;
}

ul .link {
  text-decoration: none;
  margin: 0.5rem;
  color: #fff;
  padding: 0.4rem;
}

ul .link:hover {
  background: greenyellow;
  border-radius: 10px;
}

.logo {
  font-weight: 700;
  font-style: italic;
  font-size: 16px;
  letter-spacing: 0.2rem;
  color: #fff;
  margin-left: 2rem;
  cursor: pointer;
}

.flex {
  display: flex;
}

.flex button {
  flex-basis: 300px;
  font-size: 8px;
}

.remove {
  background-color: red;
}
.rti--container {
  width: 90%;
  display: flex;
  flex-wrap: wrap;
}

.rti--tag {
  display: flex;
  width: 20%;
}
/* .rti--container * {
  background-color: red;
} */

.rti--input {
  width: 20%;
}

.rti--tag button {
  width: 30px;
}

span {
  font-size: 16px;
  font-weight: 500;
  font-style: italic;
  color: darkorange;
}
